
#table-container {
   display: flex;
      justify-content: center;
   margin: 15px;
   border-collapse: collapse;
}

#table-container table {
   border-collapse: collapse;
}

#table-container th,
#table-container td {
   padding: 5px;
   border: 1px solid #ddd;
   /* Легкая рамка для ячеек */
   text-align: left;
}

#table-container th {
   background-color: #f2f2f2;
   /* Светло-серый фон для заголовков */
   color: #333;
   /* Темно-серый текст */
}

#table-container tr:nth-child(even) {
   background-color: #f9f9f9;
   /* Чередование цвета фона строк */
}

#table-container tr:hover {
   background-color: #ddd;
   /* Цвет фона при наведении */
}

#table-container td,
#table-container th {
   border: 1px solid #ddd;
   padding: 8px;
}

#table-container th {
   padding-top: 12px;
   padding-bottom: 12px;
   text-align: left;
   background-color: #4CAF50;
   /* Зеленый фон заголовков */
   color: white;
}

.button-container {
   margin: 15px 15px 15px 15px;
   /* Отступы от краев и от таблицы */
   text-align: center;
   /* Центрирование кнопок */
}

.button-container button {
   background-color: #4CAF50;
   /* Зеленый фон */
   color: white;
   /* Белый текст */
   padding: 10px 20px;
   /* Внутренние отступы */
   margin: 0 10px;
   /* Расстояние между кнопками */
   border: none;
   /* Убрать стандартную рамку */
   border-radius: 5px;
   /* Скругление углов */
   cursor: pointer;
   /* Курсор в виде руки при наведении */
   transition: background-color 0.3s ease;
   /* Плавное изменение цвета при наведении */
}

.button-container button:hover {
   background-color: #45a049;
   /* Затемненный зеленый при наведении */
}